import { Component, Input, OnInit } from '@angular/core';
import { NgClass } from "@angular/common";

@Component({
  selector: 'app-card',
  standalone: true,
  imports: [
    NgClass
  ],
  templateUrl: './card.component.html',
  styleUrl: './card.component.css'
})

export class CardComponent implements OnInit {
  @Input() title: string = '';
  @Input() initiallyOpen: boolean = false;
  @Input() initiallyCompressed: boolean = false;
  @Input() canOpen: boolean = true;
  @Input() canCompress: boolean = false;

  private _isOpen: boolean = false;
  private _isCompressed: boolean = false;

  ngOnInit() {
    this._isOpen = this.initiallyOpen;
    this._isCompressed = this.initiallyCompressed;
  }

  get isOpen(): boolean {
    return this._isOpen;
  }

  get isCompressed(): boolean {
    return this._isCompressed;
  }

  toggleOpen() {
    if (this.canOpen) {
      this._isOpen = !this._isOpen;
    }
  }

  toggleCompression() {
    if (this.canCompress) {
      this._isCompressed = !this._isCompressed;
      this._isOpen = !this._isCompressed
    }
  }
}
